<template>
  <div class="bodyContent">
    <!-- Edit mode CASE -->
    <v-overlay :value="loadingQuery">
      <v-progress-circular
        indeterminate
        size="64"
        :width="7"
      ></v-progress-circular>
    </v-overlay>

    <!-- Missing Data when fill the form -->
    <v-dialog v-model="dataMissingModal" persistent max-width="350">
      <v-card class="pt-8" align="center">
        <v-icon align="center" color="red darken-3" size="80">
          fa-exclamation-triangle
        </v-icon>
        <p class="text_title_alert">ขออภัย พบข้อผิดพลาด</p>
        <v-card-text align="left"
          >ข้อมูลผู้ใช้ขาดหายระหว่างดำเนินการกรอกข้อมูล
          กลับสู่หน้าผู้ใช้งานหลักและเลือกแก้ไขรายงาน</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            depressed
            rounded
            large
            text
            color="red darken-1"
            @click="exitReport"
            >รับทราบ</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Confirm Case (final step to sending the case, user cann't change or edit later) -->
    <v-dialog v-model="caseSendingModal" persistent max-width="350">
      <v-card align="center" :loading="loadingCaseSending">
        <v-icon
          class="mt-8"
          align="center"
          color="light-blue lighten-2"
          size="80"
        >
          fa-file-alt
        </v-icon>
        <p class="text_title_question">ยืนยันการส่งรายงาน</p>
        <v-card-text align="left"
          >หากท่านยืนยันการส่งรายงาน รายงานฉบับดังกล่าวจะไม่สามารถแก้ไขได้อีก
          และระบบจะทำการส่งมอบให้เจ้าที่รับผิดชอบ</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            depressed
            rounded
            large
            text
            color="grey lighten-1"
            @click="caseSendingModal = false"
            :disabled="loadingCaseSending"
            >ยกเลิก</v-btn
          >
          <v-btn
            depressed
            rounded
            large
            text
            color="light-blue darken-1"
            @click="sendingCase"
            :disabled="loadingCaseSending"
            >ยืนยัน</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Confirm delete product -->
    <v-dialog v-model="productDelModal" persistent max-width="350">
      <v-card align="center" :loading="loadingCaseSending">
        <v-icon class="mt-8" align="center" color="red darken-3" size="80">
          fa-trash-alt
        </v-icon>
        <p class="text_title_alert">ยืนยันการลบผลิตถัณฑ์</p>
        <span class="text_warning_danger"
          >รหัสผลิตภัณฑ์: {{ this.temp_productID_onDel }}</span
        >
        <v-card-text align="left"
          >หากท่านยืนยันการลบผลิตถัณฑ์
          ข้อมูลผลิตภัณฑ์ดังกล่าวจะไม่สามารถกู้คืนได้อีก</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            depressed
            rounded
            large
            text
            color="grey lighten-1"
            @click="productDelModal = false"
            :disabled="loadingCaseSending"
            >ยกเลิก</v-btn
          >
          <v-btn
            depressed
            rounded
            large
            text
            color="red darken-1"
            @click="delProduct_detail"
            :disabled="loadingCaseSending"
            >ยืนยัน</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Result of sending case report to server: Success condition -->
    <v-dialog v-model="caseResultModal" persistent max-width="350">
      <v-card align="center">
        <v-icon class="mt-8" align="center" color="green lighten-2" size="80">
          fa-check
        </v-icon>
        <p class="text_title_success">ส่งรายงานสำเร็จ</p>
        <v-card-text align="left"
          >รายงานดังกล่าวถูกส่งมอบให้เจ้าที่รับผิดชอบแล้ว
          ท่านสามารถติดตามสถานะการดำเนินงานได้ที่หน้าผู้ใช้งานหลัก</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            depressed
            rounded
            large
            text
            color="green darken-1"
            @click="confirmResult"
            >ยืนยัน</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- SnackBar handler update case to server status -->
    <v-snackbar v-model="snb_updateStatus" :timeout="snb_timeout">
      {{ message_UpdateStatus }}
      <v-btn
        depressed
        :color="snb_color"
        text
        @click="snb_updateStatus = false"
      >
        ตกลง
      </v-btn>
    </v-snackbar>

    <!-- Dialog: add more allegy -->
    <v-dialog v-model="show_dialog_addAllegy" persistent max-width="400">
      <v-card align="center">
        <div class="pa-8">
          <v-icon align="center" color="#00B5F8" size="80" class="ma-6">
            fa-file-medical
          </v-icon>
          <p class="my-6">เพิ่มประวัติแพ้ยาของผู้ป่วย</p>

          <v-row align="center" justify="center">
            <v-col cols="12" class="pa-0">
              <v-text-field
                label="ชื่อยา"
                outlined
                rounded
                v-model="temp_drugAllegy"
              ></v-text-field>
            </v-col>
            <v-col cols="12" class="pa-0">
              <v-text-field
                label="อาการแพ้ยา"
                outlined
                rounded
                v-model="temp_symptom"
              ></v-text-field>
            </v-col>
            <v-col cols="12" class="pa-0">
              <v-checkbox
                class="py-0"
                v-model="isCheck_allegyCard"
                label="ผู้ป่วยมีบัตรแพ้ยาหรือไม่"
              >
              </v-checkbox>
            </v-col>
          </v-row>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              depressed
              rounded
              width="25%"
              dark
              color="grey lighten-2"
              @click="show_dialog_addAllegy = false"
              class="ma-2"
              >ยกเลิก</v-btn
            >
            <v-btn
              depressed
              rounded
              width="25%"
              dark
              color="#00B5F8"
              @click="addAllegy"
              class="ma-2"
              >เพิ่ม</v-btn
            >
          </v-card-actions>
        </div>
      </v-card>
    </v-dialog>

    <!-- Dialog: add more allegy -->
    <v-dialog v-model="show_dialog_addDisease" persistent max-width="400">
      <v-card align="center">
        <div class="pa-8">
          <v-icon align="center" color="#00B5F8" size="80" class="ma-6">
            fa-file-medical
          </v-icon>
          <p class="my-6">เพิ่มข้อมูลโรคประจำตัวของผู้ป่วย</p>

          <v-row align="center" justify="center">
            <v-col cols="12" class="pa-0">
              <v-text-field
                label="ชื่อโรคประจำตัว"
                outlined
                rounded
                v-model="temp_symptom"
              ></v-text-field>
            </v-col>
          </v-row>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              depressed
              rounded
              width="25%"
              dark
              color="grey lighten-2"
              @click="show_dialog_addDisease = false"
              class="ma-2"
              >ยกเลิก</v-btn
            >
            <v-btn
              depressed
              rounded
              width="25%"
              dark
              color="#00B5F8"
              @click="addDisease"
              class="ma-2"
              >เพิ่ม</v-btn
            >
          </v-card-actions>
        </div>
      </v-card>
    </v-dialog>

    <!-- Content forms -->
    <v-row>
      <v-col cols="12" justify="center" align="start">
        <div class="pt-6">
          <v-stepper class="elevation-0" v-model="compStep">
            <template>
              <!-- Setup stepper detail -->
              <v-stepper-header class="elevation-0">
                <v-stepper-step :complete="compStep > 1" step="1">
                  {{ stepName[0] }}
                  <small class="mt-2">มี 3 ขั้นตอน</small>
                </v-stepper-step>
                <v-divider></v-divider>
                <v-stepper-step :complete="compStep > 2" step="2">
                  {{ stepName[1] }}
                  <small class="mt-2">มี 4 ขั้นตอน</small>
                </v-stepper-step>
                <v-divider></v-divider>
                <v-stepper-step :complete="compStep > 3" step="3">{{
                  stepName[2]
                }}</v-stepper-step>
                <v-divider></v-divider>
                <v-stepper-step step="4">{{ stepName[3] }}</v-stepper-step>
              </v-stepper-header>

              <!-- Item in stepper -->
              <v-stepper-items>
                <v-stepper-content
                  class="ma-0 pa-0"
                  v-for="n in steps"
                  :key="`${n}-content`"
                  :step="n"
                >
                  <!-- Step 1: Patient Information -->
                  <div v-if="n == 1">
                    <v-stepper
                      vertical
                      class="elevation-0"
                      v-model="subCompStep"
                    >
                      <template v-for="m in subSteps">
                        <!-- Setup stepper detail -->
                        <v-stepper-step
                          :key="`${m}-step`"
                          :complete="compStep > m"
                          :step="m"
                        >
                          {{ SubStepName[m - 1] }}
                        </v-stepper-step>

                        <!-- Item in stepper -->
                        <v-stepper-content :key="`${m}-content`" :step="m">
                          <!-- Patient information -->
                          <div v-if="m == 1">
                            <!-- Patient's profile -->
                            <v-row justify="start" class="mt-4">
                              <v-col cols="12" align="left" class="pa-0 mx-4">
                                <p class="header_form">ประวัติส่วนบุคคล</p>
                              </v-col>
                            </v-row>

                            <!-- FORM: Patient -->
                            <v-form
                              ref="form"
                              v-model="valid_userInfo"
                              lazy-validation
                            >
                              <v-row align="center" class="pa-0">
                                <v-col
                                  cols="6"
                                  sm="6"
                                  md="6"
                                  align="left"
                                  class="py-0"
                                >
                                  <v-text-field
                                    label="ชื่อ"
                                    outlined
                                    rounded
                                    v-model="s1_pt_info.pt_fname"
                                  ></v-text-field>
                                </v-col>
                                <v-col
                                  cols="6"
                                  sm="6"
                                  md="6"
                                  align="left"
                                  class="py-0"
                                >
                                  <v-text-field
                                    label="นามสกุล"
                                    outlined
                                    rounded
                                    v-model="s1_pt_info.pt_lname"
                                  ></v-text-field>
                                </v-col>
                                <v-col
                                  cols="6"
                                  sm="6"
                                  md="6"
                                  align="left"
                                  class="py-0"
                                >
                                  <v-select
                                    label="เลือกเพศ *"
                                    outlined
                                    rounded
                                    v-model="s1_pt_info.pt_gender"
                                    :items="list_gender"
                                    :error-messages="userInfoErrors_gender"
                                    required
                                    @input="$v.s1_pt_info.pt_gender.$touch()"
                                    @blur="$v.s1_pt_info.pt_gender.$touch()"
                                  ></v-select>
                                </v-col>
                                <v-col
                                  cols="6"
                                  sm="6"
                                  md="6"
                                  align="left"
                                  class="py-0"
                                >
                                  <v-text-field
                                    label="อายุ (ปี) *"
                                    outlined
                                    rounded
                                    v-model="s1_pt_info.pt_age"
                                    type="number"
                                    :error-messages="userInfoErrors_age"
                                    required
                                    @input="$v.s1_pt_info.pt_age.$touch()"
                                    @blur="$v.s1_pt_info.pt_age.$touch()"
                                  ></v-text-field>
                                </v-col>
                                <v-col
                                  cols="6"
                                  sm="6"
                                  md="6"
                                  align="left"
                                  class="py-0"
                                >
                                  <v-text-field
                                    label="น้ำหนัก (กิโลกรัม) *"
                                    outlined
                                    rounded
                                    v-model="s1_pt_info.pt_weight"
                                    type="number"
                                    :error-messages="userInfoErrors_weight"
                                    required
                                    @input="$v.s1_pt_info.pt_weight.$touch()"
                                    @blur="$v.s1_pt_info.pt_weight.$touch()"
                                  ></v-text-field>
                                </v-col>
                                <v-col
                                  cols="6"
                                  sm="6"
                                  md="6"
                                  align="left"
                                  class="py-0"
                                >
                                  <v-text-field
                                    label="เบอร์โทรศัพท์ติดต่อ"
                                    outlined
                                    rounded
                                    v-mask="mask_tel"
                                    v-model="s1_pt_info.pt_telno"
                                  ></v-text-field>
                                </v-col>
                                <v-col
                                  cols="12"
                                  sm="6"
                                  md="6"
                                  align="left"
                                  class="py-0"
                                >
                                  <v-text-field
                                    label="หมายเลขประจำตัวประชาชนหรือหนังสือเดินทาง"
                                    outlined
                                    rounded
                                    v-model="s1_pt_info.pt_pid"
                                  ></v-text-field>
                                </v-col>
                                <v-col
                                  cols="12"
                                  md="6"
                                  align="left"
                                  class="py-0 mb-4"
                                >
                                  <v-checkbox
                                    class="py-0"
                                    v-model="s1_pt_info.is_checkForeigner"
                                    @change="watchNationality"
                                    label="ผู้ป่วยเป็นชาวต่างชาติหรือไม่"
                                  >
                                  </v-checkbox>
                                </v-col>
                                <v-col
                                  v-if="s1_pt_info.is_checkForeigner"
                                  cols="12"
                                  sm="8"
                                  md="6"
                                  align="left"
                                  class="py-0"
                                >
                                  <v-select
                                    label="เลือกเชื้อชาติ"
                                    outlined
                                    rounded
                                    v-model="s1_pt_info.pt_national"
                                    :items="list_nation"
                                  ></v-select>
                                </v-col>
                                <v-col
                                  v-if="s1_pt_info.pt_national == 'อื่น ๆ'"
                                  cols="12"
                                  md="8"
                                  align="left"
                                  class="py-0"
                                >
                                  <v-text-field
                                    label="ระบุเชื้อชาติ"
                                    outlined
                                    rounded
                                    v-model="s1_pt_info.pt_national_info"
                                  ></v-text-field>
                                </v-col>
                              </v-row>
                            </v-form>

                            <v-divider></v-divider>

                            <!-- Patient's address -->
                            <v-row justify="start" class="mt-4">
                              <v-col cols="12" align="left" class="pa-0 mx-4">
                                <p class="header_form">
                                  ข้อมูลสถานที่อยู่ของผู้ป่วย
                                </p>
                              </v-col>
                            </v-row>

                            <!-- FORM: Address -->
                            <v-row justify="start">
                              <v-col
                                cols="12"
                                md="12"
                                class="pt-0 px-6"
                                align="left"
                              >
                                <v-row class="pt-6">
                                  <v-col cols="12" class="py-0">
                                    <v-text-field
                                      label="ที่อยู่"
                                      outlined
                                      rounded
                                      v-model="s1_pt_address.pt_ads_address"
                                    ></v-text-field>
                                  </v-col>
                                </v-row>

                                <v-row>
                                  <v-col cols="12" sm="6" md="6" class="py-0">
                                    <v-autocomplete
                                      label=" เลือกจังหวัด"
                                      item-text="arrProvince"
                                      :items="arrProvince"
                                      :change="selectProvince"
                                      outlined
                                      rounded
                                      v-model="s1_pt_address.pt_ads_province"
                                    ></v-autocomplete>
                                  </v-col>

                                  <v-col cols="12" sm="6" md="6" class="py-0">
                                    <v-autocomplete
                                      label="เลือกอำเภอ/เขต"
                                      item-text="arrDistrict"
                                      :items="arrDistrict"
                                      :change="selectDistrict"
                                      outlined
                                      rounded
                                      v-model="s1_pt_address.pt_ads_district"
                                    ></v-autocomplete>
                                  </v-col>

                                  <v-col cols="12" sm="6" md="6" class="py-0">
                                    <v-autocomplete
                                      label="เลือกตำบล/แขวง"
                                      item-text="arrSubDistrict"
                                      :items="arrSubDistrict"
                                      outlined
                                      rounded
                                      v-model="s1_pt_address.pt_ads_subDistrict"
                                    ></v-autocomplete>
                                  </v-col>
                                </v-row>
                              </v-col>
                            </v-row>

                            <!-- Button onNextStep or Cancel -->
                            <v-row justify="center">
                              <v-btn
                                depressed
                                rounded
                                large
                                class="ma-8"
                                width="25%"
                                color="gray"
                                @click="exitReport()"
                                >ยกเลิก</v-btn
                              >
                              <v-btn
                                depressed
                                rounded
                                large
                                class="ma-8"
                                width="25%"
                                color="primary"
                                @click="nextSubStep(m)"
                                :disabled="!valid_userInfo"
                                >ถัดไป</v-btn
                              >
                            </v-row>
                          </div>

                          <!-- Patient's history of cure -->
                          <div v-if="m == 2">
                            <!-- Patient's Disease -->
                            <v-row justify="start" class="mt-4">
                              <v-col cols="12" align="left" class="pl-3">
                                <p class="header_form">
                                  ประวัติแพ้ยาและโรคประจำตัว
                                </p>
                              </v-col>
                            </v-row>

                            <v-row class="px-6">
                              <v-col cols="12" class="py-0">
                                <v-checkbox
                                  class="py-0"
                                  v-model="is_checkDrugAllegy"
                                  @change="watchDrugAllegy"
                                  label="ผู้ป่วยมีประวัติแพ้ยาหรือไม่"
                                >
                                </v-checkbox>
                              </v-col>
                            </v-row>
                            <div v-if="is_checkDrugAllegy">
                              <!-- allegy detail -->
                              <v-row
                                v-for="(content,
                                index) in s1_his_arr_drugAllegy"
                                :key="index"
                              >
                                <v-col cols="12" md="1" class="pt-4">
                                  <span>ลำดับที่ {{ index + 1 }}</span>
                                  <v-divider class="mb-4"> </v-divider>
                                </v-col>
                                <v-col cols="12" md="3" class="py-0">
                                  <v-text-field
                                    class="my-0"
                                    label="ชื่อยา"
                                    outlined
                                    rounded
                                    v-model="content.drug"
                                    readonly
                                  ></v-text-field>
                                </v-col>
                                <v-col cols="12" md="3" class="py-0">
                                  <v-text-field
                                    class="my-0"
                                    label="ชื่ออาการแพ้ยา"
                                    outlined
                                    rounded
                                    v-model="content.symptom"
                                    readonly
                                  ></v-text-field>
                                </v-col>
                                <v-col cols="12" md="3" class="py-0 ml-1">
                                  <v-checkbox
                                    class="mt-3"
                                    label="มีบัตรแพ้ยา"
                                    v-model="content.allegyCard"
                                    readonly
                                  ></v-checkbox>
                                </v-col>
                                <v-col
                                  cols="12"
                                  md="1"
                                  class="pa-0 pr-5 pt-2"
                                  align="right"
                                >
                                  <v-btn depressed small fab>
                                    <v-icon
                                      small
                                      align="center"
                                      color="red darken-1"
                                      @click="deleteAllegy(index)"
                                    >
                                      fa-trash-alt
                                    </v-icon>
                                  </v-btn>
                                </v-col>
                                <v-col cols="12" md="12">
                                  <v-divider class="mb-5"> </v-divider>
                                </v-col>
                              </v-row>
                              <v-col cols="12">
                                <v-row align="center">
                                  <v-btn
                                    block
                                    depressed
                                    @click="show_dialog_addAllegy = true"
                                  >
                                    <v-icon
                                      small
                                      align="center"
                                      color="primary"
                                      class="pa-3"
                                    >
                                      fa-plus </v-icon
                                    >เพิ่มประวัติแพ้ยา
                                  </v-btn>
                                </v-row>
                              </v-col>
                            </div>

                            <v-row class="px-6">
                              <v-col cols="6" class="py-0">
                                <v-checkbox
                                  class="py-0"
                                  v-model="is_checkDisease"
                                  @change="watchDisease"
                                  label="ผู้ป่วยมีโรคประจำตัวหรือไม่"
                                >
                                </v-checkbox>
                              </v-col>
                            </v-row>
                            <div v-if="is_checkDisease">
                              <!-- disease detail -->
                              <v-row
                                v-for="(content, index) in s1_his_arr_disease"
                                :key="index"
                              >
                                <v-col cols="12" md="1" class="pt-4">
                                  <span>ลำดับที่ {{ index + 1 }}</span>
                                  <v-divider class="mb-4"> </v-divider>
                                </v-col>
                                <v-col cols="12" md="9" class="py-0">
                                  <v-text-field
                                    class="my-0"
                                    label="ชื่อโรคประจำตัว"
                                    outlined
                                    rounded
                                    v-model="content.symptom"
                                    readonly
                                  ></v-text-field>
                                </v-col>
                                <v-col
                                  cols="12"
                                  md="1"
                                  class="pa-0 pr-5 pt-2"
                                  align="right"
                                >
                                  <v-btn depressed small fab>
                                    <v-icon
                                      small
                                      align="center"
                                      color="red darken-1"
                                      @click="deleteDisease(index)"
                                    >
                                      fa-trash-alt
                                    </v-icon>
                                  </v-btn>
                                </v-col>
                                <v-col cols="12" md="12">
                                  <v-divider class="mb-5"> </v-divider>
                                </v-col>
                              </v-row>
                              <v-row align="center">
                                <v-btn
                                  block
                                  depressed
                                  @click="show_dialog_addDisease = true"
                                >
                                  <v-icon
                                    small
                                    align="center"
                                    color="primary"
                                    class="pa-3"
                                  >
                                    fa-plus
                                  </v-icon>
                                  เพิ่มข้อมูลโรคประจำตัว
                                </v-btn>
                              </v-row>
                            </div>

                            <v-divider class="mt-6"></v-divider>
                            <!-- Patient's history of cured -->
                            <v-row justify="start" class="mt-4">
                              <v-col cols="12" align="left" class="pa-0 mx-4">
                                <p class="header_form">
                                  ประวัติการรักษาและภาวะอื่นที่เกี่ยวข้อง
                                </p>
                              </v-col>
                            </v-row>
                            <v-row class="py-4 px-6">
                              <v-col cols="12" md="6" class="py-0">
                                <v-textarea
                                  class="rounded-custom"
                                  label="สถานพยาบาลที่ทำการรักษาประจำ"
                                  auto-grow
                                  outlined
                                  rounded
                                  v-model="s1_pt_history.pt_his_favHospital"
                                ></v-textarea>
                              </v-col>
                              <v-col cols="12" md="6" class="py-0">
                                <v-textarea
                                  class="rounded-custom"
                                  label="ระบุภาวะอื่นที่เกี่ยวข้อง เช่น การตั้งครรถ์ ให้นมบุตร G6PD หรืออื่น ๆ ให้ระบุ"
                                  auto-grow
                                  outlined
                                  rounded
                                  v-model="s1_pt_history.pt_his_conditionInfo"
                                ></v-textarea>
                              </v-col>
                            </v-row>

                            <!-- Button onNextStep or Cancel -->
                            <v-row justify="center">
                              <v-btn
                                depressed
                                rounded
                                large
                                class="ma-8"
                                width="25%"
                                color="gray"
                                @click="backSubStep(m)"
                                >ย้อนกลับ</v-btn
                              >
                              <v-btn
                                depressed
                                rounded
                                large
                                class="ma-8"
                                width="25%"
                                color="primary"
                                @click="nextSubStep(m)"
                                >ถัดไป</v-btn
                              >
                            </v-row>
                          </div>

                          <!-- UnSymptom Detail and Discovery -->
                          <div v-if="m == 3">
                            <v-row justify="start" class="mt-4">
                              <v-col cols="12" align="left" class="pa-0 mx-4">
                                <!-- Photo(s) -->
                                <v-row justify="center" class="mx-4">
                                  <v-col cols="12" class="pt-0" align="left">
                                    <div>
                                      <span class="mr-6"
                                        >รูปภาพอาการไม่พึงประสงค์ <br
                                      /></span>
                                      <span
                                        v-if="!imgStatus"
                                        class="text_status_imgFailed"
                                        >(สถานะ ยังไม่บันทึก) <br
                                      /></span>
                                      <span
                                        v-else
                                        class="text_status_imgSuccess"
                                        >(สถานะ บันทึกสำเร็จ) <br
                                      /></span>
                                      <span class="text_warning_danger">
                                        ข้อควรระวัง: รูปภาพจะไม่ถูกบันทึก
                                        หากไม่กดปุ่มบันทึกรูปภาพ <br
                                      /></span>
                                      <v-alert
                                        dismissible
                                        transition="scroll-x-transition"
                                        v-if="imgNotfound"
                                        type="error"
                                        class="mt-5"
                                      >
                                        กรุณาเลือกรูปภาพอย่างน้อยจำนวน 1 รูปภาพ
                                      </v-alert>
                                      <v-row>
                                        <v-col cols="12" md="6">
                                          <v-file-input
                                            outlined
                                            rounded
                                            v-model="s1_st_photos"
                                            show-size
                                            counter
                                            multiple
                                            prepend-inner-icon="$file"
                                            prepend-icon=""
                                            accept="image/*"
                                            label="คลิกเพื่อเพิ่มรูปภาพ"
                                          >
                                            <template
                                              v-slot:selection="{ text }"
                                            >
                                              <v-chip color="primary">
                                                {{ text }}
                                              </v-chip>
                                            </template>
                                          </v-file-input>
                                        </v-col>
                                        <v-col
                                          cols="12"
                                          md="6"
                                          class="mt-1"
                                          justify="center"
                                          align="center"
                                        >
                                          <v-btn
                                            block
                                            depressed
                                            rounded
                                            large
                                            color="primary"
                                            @click="uploadIMG"
                                            >บันทึกรูปภาพ</v-btn
                                          >
                                        </v-col>
                                      </v-row>
                                      <v-card
                                        outlined
                                        justify="center"
                                        align="center"
                                      >
                                        <div
                                          v-if="s1_photos_show.length == 0"
                                          justify="center"
                                          align="center"
                                          class="pa-5"
                                        >
                                          <v-icon
                                            class="py-5"
                                            size="130"
                                            color="#CDCDCD"
                                            >fa-images</v-icon
                                          >
                                          <p>
                                            พื้นที่แสดงรูปภาพอาการไม่พึงประสงค์
                                            (รูปภาพจะปรากฎเมื่อทำการอัปโหลดสำเร็จ)
                                          </p>
                                          <span class="subtitle"
                                            >การอัปโหลดรูปภาพมายัง Tawai for
                                            Health</span
                                          >
                                          <br />
                                          <span class="subtitle"
                                            >ข้าพเจ้ายอมรับ ข้อตกลง Terms of Use
                                            and Privacy Policy ของทาง
                                            TaWai</span
                                          >
                                        </div>
                                        <div v-else class="pa-10">
                                          <v-row>
                                            <v-col
                                              v-for="(n,
                                              index) in s1_photos_show"
                                              :key="index"
                                              class="d-flex child-flex"
                                              cols="3"
                                            >
                                              <v-img
                                                :src="n"
                                                contain
                                                aspect-ratio="1.3"
                                                class="grey lighten-2"
                                              >
                                              </v-img>
                                            </v-col>
                                          </v-row>
                                        </div>
                                      </v-card>
                                    </div>
                                  </v-col>
                                </v-row>
                              </v-col>
                            </v-row>

                            <!-- Dangerous level detail -->
                            <v-row justify="start" class="mt-4">
                              <v-col cols="12" align="left" class="pa-0 mx-4">
                                <p class="header_form">
                                  รายละเอียดและความร้ายแรงของอาการ
                                </p>
                              </v-col>
                            </v-row>
                            <v-row class="pt-6">
                              <!-- unSymptom detail -->
                              <v-col cols="12" md="6" align="left" class="py-0">
                                <v-text-field
                                  label="รายละเอียดอาการ"
                                  outlined
                                  rounded
                                  v-model="s1_st_info.st_symptomInfo"
                                ></v-text-field>
                              </v-col>
                              <!-- Date of Found -->
                              <v-col cols="12" md="6" align="left" class="py-0">
                                <v-menu
                                  v-model="menu_date"
                                  :close-on-content-click="false"
                                  :nudge-right="40"
                                  transition="scale-transition"
                                  offset-y
                                  max-width="290px"
                                  min-width="290px"
                                >
                                  <template v-slot:activator="{ on }">
                                    <v-text-field
                                      :value="DateFormatted"
                                      outlined
                                      rounded
                                      label="วันที่พบอาการ"
                                      readonly
                                      v-on="on"
                                    ></v-text-field>
                                  </template>
                                  <v-date-picker
                                    v-model="s1_st_info.st_date"
                                    locale="th"
                                    no-title
                                    scrollable
                                    @input="menu_date = false"
                                  >
                                  </v-date-picker>
                                </v-menu>
                              </v-col>
                            </v-row>

                            <v-row class="">
                              <v-col cols="12" md="6" align="left" class="py-0">
                                <v-select
                                  label="เลือกระดับความรุนแรง"
                                  outlined
                                  rounded
                                  v-model="s1_st_info.st_dangerType"
                                  :items="list_dangerType"
                                  :change="selectDangerType"
                                ></v-select>
                              </v-col>
                              <v-col
                                v-if="
                                  s1_st_info.st_dangerType == 'ร้ายแรง' ||
                                    s1_st_info.st_dangerType == 'ไม่ร้ายแรง'
                                "
                                cols="12"
                                md="6"
                                align="left"
                                class="py-0"
                              >
                                <v-select
                                  label="เลือกประเภทความร้ายแรง"
                                  outlined
                                  rounded
                                  v-model="s1_st_info.st_dangerList"
                                  :items="list_dangerList"
                                ></v-select>
                              </v-col>
                              <!-- <v-col v-if="s1_st_info.st_dangerList == 'อื่น ๆ ที่มีความสำคัญทางการแพทย์'"
                                cols="6" align="left" class="py-0">
                                <v-text-field
                                  label="กรอกรายละเอียดความสำคัญทางการแพทย์"
                                  outlined rounded
                                  v-model="s1_pt_info.st_dangerSeverityDetail"
                                ></v-text-field>
                              </v-col> -->
                            </v-row>

                            <!-- Treatment Detail -->
                            <div
                              v-if="s1_st_info.st_dangerType == 'ไม่ร้ายแรง'"
                            >
                              <v-row justify="start" class="mt-4">
                                <v-col cols="12" align="left" class="pa-0 mx-4">
                                  <p class="header_form">การรักษาพยาบาล</p>
                                </v-col>
                              </v-row>
                              <v-row class="pa-0">
                                <v-col cols="12" md="6" class="py-0">
                                  <v-select
                                    label="สถานะการรักษาพยาบาล"
                                    outlined
                                    rounded
                                    v-model="s1_st_info.st_treatment"
                                    :items="list_treatment"
                                    :change="watchTreatment"
                                  ></v-select>
                                </v-col>
                                <v-col
                                  v-if="
                                    s1_st_info.st_treatment_ID == 1 ||
                                      s1_st_info.st_treatment_ID == 2
                                  "
                                  cols="12"
                                  md="6"
                                  class="py-0"
                                >
                                  <v-text-field
                                    label="ระบุโรงพยาบาล"
                                    outlined
                                    rounded
                                    v-model="s1_st_info.st_hospital"
                                  ></v-text-field>
                                </v-col>
                                <v-col
                                  v-if="
                                    s1_st_info.st_treatment_ID == 1 ||
                                      s1_st_info.st_treatment_ID == 2
                                  "
                                  cols="6"
                                  md="6"
                                  class="py-0"
                                >
                                  <v-autocomplete
                                    label=" เลือกจังหวัด"
                                    item-text="arrProvince"
                                    :items="arrProvince"
                                    outlined
                                    rounded
                                    v-model="s1_st_info.st_hospital_province"
                                  ></v-autocomplete>
                                </v-col>
                              </v-row>
                            </div>

                            <v-divider></v-divider>

                            <v-row justify="start" class="mt-4">
                              <v-col cols="12" align="left" class="pa-0 mx-4">
                                <p class="header_form">สถานที่พบอาการ</p>
                              </v-col>
                            </v-row>

                            <v-form
                              ref="form"
                              v-model="valid_symptomAddress"
                              lazy-validation
                            >
                              <v-row justify="start">
                                <v-col
                                  cols="12"
                                  md="12"
                                  class="pt-0 px-6"
                                  align="left"
                                >
                                  <v-row class="pt-6">
                                    <v-col cols="12" class="py-0">
                                      <v-text-field
                                        label="ที่อยู่"
                                        outlined
                                        rounded
                                        v-model="s1_st_discovery.st_dis_address"
                                        :error-messages="AddressErrors"
                                        required
                                        @input="
                                          $v.s1_st_discovery.st_dis_address.$touch()
                                        "
                                        @blur="
                                          $v.s1_st_discovery.st_dis_address.$touch()
                                        "
                                      ></v-text-field>
                                    </v-col>
                                  </v-row>

                                  <v-row>
                                    <v-col cols="6" md="6" class="py-0">
                                      <v-autocomplete
                                        label=" เลือกจังหวัด"
                                        item-text="arrProvince"
                                        :items="arrProvince"
                                        :change="selectProvince_disSymptom"
                                        outlined
                                        rounded
                                        v-model="
                                          s1_st_discovery.st_dis_province
                                        "
                                        :error-messages="provErrors"
                                        required
                                        @input="
                                          $v.s1_st_discovery.st_dis_province.$touch()
                                        "
                                        @blur="
                                          $v.s1_st_discovery.st_dis_province.$touch()
                                        "
                                      ></v-autocomplete>
                                    </v-col>

                                    <v-col cols="6" md="6" class="py-0">
                                      <v-autocomplete
                                        label="เลือกอำเภอ/เขต"
                                        item-text="arrDistrict"
                                        :items="arrDistrict_symp"
                                        :change="selectDistrict_disSymptom"
                                        outlined
                                        rounded
                                        v-model="
                                          s1_st_discovery.st_dis_district
                                        "
                                        :error-messages="districtErrors"
                                        required
                                        @input="
                                          $v.s1_st_discovery.st_dis_district.$touch()
                                        "
                                        @blur="
                                          $v.s1_st_discovery.st_dis_district.$touch()
                                        "
                                      ></v-autocomplete>
                                    </v-col>

                                    <v-col cols="6" md="6" class="py-0">
                                      <v-autocomplete
                                        label="เลือกตำบล/แขวง"
                                        item-text="arrSubDistrict"
                                        :items="arrSubDistrict_symp"
                                        outlined
                                        rounded
                                        v-model="
                                          s1_st_discovery.st_dis_subDistrict
                                        "
                                        :error-messages="subDistrictErrors"
                                        required
                                        @input="
                                          $v.s1_st_discovery.st_dis_subDistrict.$touch()
                                        "
                                        @blur="
                                          $v.s1_st_discovery.st_dis_subDistrict.$touch()
                                        "
                                      ></v-autocomplete>
                                    </v-col>
                                  </v-row>
                                </v-col>
                              </v-row>
                            </v-form>

                            <!-- Button onNextStep or Cancel -->
                            <v-row justify="center">
                              <v-btn
                                depressed
                                rounded
                                large
                                class="ma-8"
                                width="25%"
                                color="gray"
                                @click="backSubStep(m)"
                                >ย้อนกลับ</v-btn
                              >
                              <v-btn
                                depressed
                                rounded
                                large
                                class="ma-8"
                                width="25%"
                                color="primary"
                                @click="nextStep(n)"
                                >ถัดไป</v-btn
                              >
                            </v-row>
                          </div>
                        </v-stepper-content>
                      </template>
                    </v-stepper>
                  </div>

                  <!-- Step 2: Product information -->
                  <div v-else-if="n == 2">
                    <!-- Add Product Suspect -->
                    <div v-if="!addProduct" class="pa-5">
                      <v-row justify="start">
                        <v-col cols="12" align="left">
                          <p class="header_form">
                            ผลิตภัณฑ์ที่อาจส่งผลต่ออาการไม่พึงประสงค์
                          </p>
                        </v-col>
                      </v-row>
                      <!-- Render Product related with the UnSymptom case -->
                      <v-row>
                        <v-col cols="2" class="py-0">
                          <span>รหัสผลิตภัณฑ์</span>
                        </v-col>
                        <v-col cols="2" class="py-0">
                          <span>ประเภทผลิตภัณฑ์</span>
                        </v-col>
                        <v-col cols="6" class="py-0">
                          <span>ชื่อผลิตภัณฑ์</span>
                        </v-col>
                        <v-col cols="2" class="py-0">
                          <span>การจัดการ</span>
                        </v-col>
                      </v-row>
                      <div v-if="list_product.length > 0" class="mt-0 mb-6">
                        <v-row
                          v-for="(content, index) in list_product"
                          :key="index"
                        >
                          <v-col cols="12" class="py-0">
                            <v-divider class="mx-8 mt-2 mb-4"></v-divider>
                          </v-col>
                          <v-col cols="2" class="py-0 mt-1">
                            <span>{{ content.DrugRelated }}</span>
                          </v-col>
                          <v-col cols="2" class="py-0">
                            <v-chip color="#00B5F8" text-color="white">
                              <v-avatar left>
                                <v-icon size="16">{{
                                  list_pdTypeIcon[content.ProductTypeId - 1]
                                }}</v-icon>
                              </v-avatar>
                              {{ content.ProductTypeName }}
                            </v-chip>
                          </v-col>
                          <v-col cols="6" class="py-0 mt-1">
                            <span>{{ content.ProductName }}</span>
                          </v-col>
                          <v-col cols="2" class="py-0">
                            <v-icon
                              class="mx-2 my-1"
                              color="dark-grey"
                              @click="editProduct_detail(index)"
                            >
                              mdi-pencil-circle
                            </v-icon>
                            <v-icon
                              class="mx-2 my-1"
                              color="pink"
                              @click="delProduct_confirm(index)"
                            >
                              mdi-delete
                            </v-icon>
                          </v-col>
                        </v-row>
                      </div>
                      <div v-else class="my-6 subtitle_bold" align="center">
                        <span
                          >ไม่พบรายการผลิตภัณฑ์ กรุณาเพิ่มข้อมูลผลิตภัณฑ์</span
                        >
                      </div>
                      <!-- Button add more product -->
                      <v-row>
                        <v-btn
                          block
                          depressed
                          rounded
                          large
                          width="25%"
                          color="gray"
                          @click="addProduct_detail"
                          >เพิ่มข้อมูลผลิตภัณฑ์</v-btn
                        >
                      </v-row>
                      <!-- Button onNextStep or Cancel -->
                      <v-row class="mt-6" justify="center">
                        <v-btn
                          depressed
                          rounded
                          large
                          class="ma-8"
                          width="25%"
                          color="gray"
                          @click="backStep(n)"
                          >ย้อนกลับ</v-btn
                        >
                        <v-btn
                          depressed
                          rounded
                          large
                          class="ma-8"
                          width="25%"
                          color="primary"
                          @click="nextStep(n)"
                          >สรุปรายงาน</v-btn
                        >
                      </v-row>
                    </div>

                    <!-- Product form -->
                    <div v-else>
                      <v-row justify="center" class="pa-0">
                        <ProductInfo
                          :caseProperties="caseProperties"
                          :s1_user_info="s1_user_info"
                          :s2_pd_detail="s2_pd_detail"
                          :s2_pd_label="s2_pd_label"
                          :s2_pd_licenseInfo="s2_pd_licenseInfo"
                          :s2_pd_manufacturer="s2_pd_manufacturer"
                          :s2_pd_usage="s2_pd_usage"
                          :s2_pd_check="s2_pd_check"
                          :s2_pd_checkInfo="s2_pd_checkInfo"
                          :s2_drugSet="s2_drugSet"
                          :s2_testkit="s2_testkit"
                          :pd_photo_show="s2_photos_show"
                          :s2_pd_duration="s2_pd_duration"
                          :s2_pd_related="s2_pd_related"
                        />
                      </v-row>
                    </div>
                  </div>

                  <!-- Step 3: Source of product information -->
                  <div v-else-if="n == 3">
                    <v-row justify="center" class="pa-4">
                      <ProductSource
                        :s3_src_detail="s3_src_detail"
                        :src_photo_show="s3_photos_show"
                      />
                    </v-row>
                  </div>

                  <!-- Step 4: Summary Report of Unsymptom -->
                  <div v-else-if="n == 4">
                    <v-row justify="center" class="pa-4">
                      <ReportSymptom
                        :s1_pt_info="s1_pt_info"
                        :s1_pt_address="s1_pt_address"
                        :s1_his_arr_disease="s1_his_arr_disease"
                        :s1_his_arr_drugAllegy="s1_his_arr_drugAllegy"
                        :s1_pt_history="s1_pt_history"
                        :s1_photos_show="s1_photos_show"
                        :s1_st_info="s1_st_info"
                        :s1_st_discovery="s1_st_discovery"
                        :s2_3_information="s2_3_information"
                      />
                    </v-row>
                    <v-row justify="center">
                      <v-btn
                        depressed
                        rounded
                        large
                        class="ma-8"
                        width="25%"
                        color="gray"
                        @click="backStep(n)"
                        >ย้อนกลับ</v-btn
                      >
                      <v-btn
                        depressed
                        rounded
                        large
                        class="ma-8"
                        width="25%"
                        color="primary"
                        @click="nextStep(n)"
                        >ส่งรายงาน</v-btn
                      >
                    </v-row>
                  </div>
                </v-stepper-content>
              </v-stepper-items>
            </template>
          </v-stepper>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, maxLength } from "vuelidate/lib/validators";

import { mask } from "vue-the-mask";
import moment from "moment";

import EventBus from "./EventBus";
import ProductInfo from "./form/ProductInfo";
import ProductSource from "./form/ProductSource";
import ReportSymptom from "./display/ReportSymptom";

export default {
  components: {
    ProductInfo,
    ProductSource,
    ReportSymptom
  },
  mixins: [validationMixin],
  directives: { mask },
  validations: {
    s1_pt_info: {
      pt_gender: { required },
      pt_age: { required, maxLength: maxLength(3) },
      pt_weight: { required, maxLength: maxLength(3) }
    },
    s1_st_discovery: {
      st_dis_address: { required },
      st_dis_province: { required },
      st_dis_district: { required },
      st_dis_subDistrict: { required }
    }
  },
  data: () => ({
    userProperties: {},
    caseProperties: {},
    compStep: 1,
    steps: 4,
    subCompStep: 1,
    subSteps: 3,
    stepName: ["ข้อมูลผู้ป่วย", "ข้อมูลผลิตภัณฑ์", "แหล่งที่มา", "สรุปรายงาน"],
    SubStepName: [
      "ข้อมูลผู้ป่วย",
      "ประวัติการรักษาของผู้ป่วย",
      "ข้อมูลอาการและสถานที่พบ"
    ],

    // CASE AND USER PROPERTIES CONTROL, DIALOG ALREADY INCLUDE
    dataMissingModal: false,
    caseSendingModal: false,
    caseResultModal: false,
    productDelModal: false,
    snb_updateStatus: false,
    message_UpdateStatus: "",
    snb_color: "",
    snb_timeout: 2000,
    editMODE: true,
    loadingCaseSending: false,
    show_dialog_addAllegy: false,
    show_dialog_addDisease: false,

    // VARIABLES FOR DIALOG
    loadingQuery: false,
    temp_drugAllegy: "",
    temp_symptom: "",
    isCheck_allegyCard: false,

    // Valid Form Check
    valid_userInfo: false,
    valid_symptomAddress: false,

    // LIST STORAGE
    mask_tel: "###-###-####",
    mask_pid: ["#-####-#####-##-#", "X-#######", "XX-#######"],
    list_pdTypeIcon: [
      "fa-pills",
      "fa-capsules",
      "fa-seedling",
      "fa-tablets",
      "fa-prescription-bottle",
      "fa-stethoscope",
      "fa-magic",
      "fa-utensils",
      "fa-vial",
      "fa-skull"
    ],
    arrProvinceID: [],
    arrDistrictID: [],
    arrSubDistrictID: [],
    arrProvince: [],
    arrDistrict: [],
    arrSubDistrict: [],
    arrDistrictID_symp: [],
    arrSubDistrictID_symp: [],
    arrDistrict_symp: [],
    arrSubDistrict_symp: [],
    list_gender: [],
    list_nation: [],
    list_dangerType: [],
    list_dangerList: [],
    list_dangerList_ID: [],
    list_treatment: [],
    list_product: [],
    maxdate: new Date().toISOString().substr(0, 10),
    menu_date: false,

    // EXTRA VARIABLE FOR UPDATE ANOTHER DEPENDENCIES
    temp_productID_onEdit: "",
    temp_productID_onDel: "",

    // UnSymptom Images
    imgNotfound: false,
    imgStatus: false,
    s1_st_photos: [],
    s1_photos_show: [],

    // STEP01, subStep01
    s1_pt_info: {
      pt_fname: "",
      pt_lname: "",
      pt_gender: "",
      pt_age: "",
      pt_weight: "",
      pt_pid: "",
      pt_telno: "",
      is_checkForeigner: false,
      pt_national: "",
      pt_national_info: ""
    },
    s1_pt_address: {
      pt_ads_address: "",
      pt_ads_province: "",
      pt_ads_district: "",
      pt_ads_subDistrict: ""
    },
    // STEP01, subStep02
    is_checkDrugAllegy: false,
    is_checkDisease: false,
    s1_his_arr_drugAllegy: [],
    s1_his_arr_disease: [],
    s1_pt_history: {
      pt_his_favHospital: "",
      pt_his_conditionInfo: ""
    },
    // STEP01, subStep03
    s1_st_info: {
      st_symptomInfo: "",
      st_date: "",
      st_dangerType: "",
      st_dangerList: "",
      st_dangerList_ID: 10,
      st_dangerSeverityDetail: "",
      st_treatment: "",
      st_treatment_ID: 3,
      st_hospital: "",
      st_hospital_province: ""
    },
    s1_st_discovery: {
      st_dis_address: "",
      st_dis_province: "",
      st_dis_district: "",
      st_dis_subDistrict: ""
    },

    // STEP02, Add Product Suspect
    // View Controller
    addProduct: false,

    // Product Detail
    s1_user_info: {
      us_hasUser: true
    },
    s2_pd_detail: {
      pd_pdTypeName: "",
      pd_pdTypeIndex: 1,
      pd_nameTH: "",
      pd_cNameTH: "",
      pd_cNameEN: "",
      pd_fNameTH: "",
      pd_fNameEN: "",
      pd_hpTypeName: "",
      pd_hpTypeIndex: 1,
      pd_hpInfo: ""
    },
    s2_pd_label: {
      pd_hasLabel: false,
      pd_hasLicense: false
    },
    s2_pd_licenseInfo: {
      pdlc_number: "",
      pdlc_status: "",
      pdlc_isMatch: "0"
    },
    s2_pd_manufacturer: {
      pd_mf_owner: "",
      pd_mf_name: "",
      pd_mf_address: "",
      pd_mf_lot: "",
      pd_mf_mfd: "",
      pd_mf_exd: "",
      pd_mf_newcode: ""
    },
    s2_pd_usage: {
      pd_us_KindName: "",
      pd_us_KindIndex: 0,
      pd_us_KindInfo: "",
      pd_us_InstName: "",
      pd_us_InstIndex: 0,
      pd_us_InstInfo: "",
      pd_us_volume: "",
      pd_us_price: "",
      pd_us_expected: "",
      pd_us_labelInfo: ""
    },
    s2_pd_check: {
      isCheckDMSC: false,
      isCheckQuality: false,
      isCheckAds: false,
      isCheckTestkit: false
    },
    s2_pd_checkInfo: {
      check_DMSC_status: "0",
      check_DMSC_info: "",
      check_quality_status: "0",
      check_quality_info: "",
      check_ads_status: "0",
      check_ads_info: ""
    },
    s2_drugSet: [],
    s2_testkit: [],
    s2_photos_show: [],

    s2_pd_duration: {
      pd_dur_dateOfUse: "",
      pd_dur_dateOfStop: "",
      pd_dur_contUse: true
    },
    s2_pd_related: {
      isCheck_related: true,
      pd_check_relatedID: 4,
      pd_check_relatedName: ""
    },

    // Source of Product Detail
    s3_src_detail: {
      src_srcTypeName: "",
      src_srcTypeIndex: 0,
      src_subSrcTypeName: "",
      src_subSrcTypeIndex: 0,
      src_subInfoName: "",
      src_dateOnAir: "",
      src_timeOnAir: "",
      src_adrs: "",
      src_prov: "",
      src_dist: "",
      src_subDist: "",
      src_pName: "",
      src_pTel: "",
      src_pContact: "",
      src_pLicense: "",
      src_pCar: "",
      src_pCarColor: ""
    },
    s3_photos_show: [],

    // For Summary Report
    s2_3_information: []
  }),
  computed: {
    userInfoErrors_gender() {
      const errors = [];
      if (!this.$v.s1_pt_info.pt_gender.$dirty) return errors;
      !this.$v.s1_pt_info.pt_gender.required && errors.push("กรุณาระบุเพศ");
      return errors;
    },
    userInfoErrors_age() {
      const errors = [];
      if (!this.$v.s1_pt_info.pt_age.$dirty) return errors;
      !this.$v.s1_pt_info.pt_age.maxLength &&
        errors.push("กรุณาระบุข้อมูลให้ถูกต้อง");
      !this.$v.s1_pt_info.pt_age.required && errors.push("กรุณาระบุอายุ");
      return errors;
    },
    userInfoErrors_weight() {
      const errors = [];
      if (!this.$v.s1_pt_info.pt_weight.$dirty) return errors;
      !this.$v.s1_pt_info.pt_weight.maxLength &&
        errors.push("กรุณาระบุข้อมูลให้ถูกต้อง");
      !this.$v.s1_pt_info.pt_weight.required && errors.push("กรุณาระบุน้ำหนัก");
      return errors;
    },
    selectProvince() {
      if (this.s1_pt_address.pt_ads_province.length > 0) {
        this.fetchDistrict();
      }
      return 0;
    },
    selectDistrict() {
      if (this.s1_pt_address.pt_ads_district.length > 0) {
        this.fetchSubDistrict();
      }
      return 0;
    },
    selectDangerType() {
      if (this.s1_st_info.st_dangerType.length > 0) {
        if (this.s1_st_info.st_dangerType == "ไม่สามารถระบุได้") {
          this.s1_st_info.st_dangerList_ID == 10;
        } else {
          this.fetchDangerList();
        }
      }
      return 0;
    },
    DateFormatted() {
      let originYear = this.s1_st_info.st_date.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s1_st_info.st_date.replace(originYear, cvYearBE);
      return this.s1_st_info.st_date
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    },
    watchTreatment() {
      if (this.s1_st_info.st_treatment.length > 0) {
        this.selectTreatment();
      }
      return 0;
    },
    selectProvince_disSymptom() {
      if (this.s1_st_discovery.st_dis_province.length > 0) {
        this.fetchDistrict_disSymp();
      }
      return 0;
    },
    selectDistrict_disSymptom() {
      if (this.s1_st_discovery.st_dis_district.length > 0) {
        this.fetchSubDistrict_disSymp();
      }
      return 0;
    },
    AddressErrors() {
      const errors = [];
      if (!this.$v.s1_st_discovery.st_dis_address.$dirty) return errors;
      !this.$v.s1_st_discovery.st_dis_address.required &&
        errors.push("กรุณาระบุที่พบอาการ");
      return errors;
    },
    provErrors() {
      const errors = [];
      if (!this.$v.s1_st_discovery.st_dis_province.$dirty) return errors;
      !this.$v.s1_st_discovery.st_dis_province.required &&
        errors.push("กรุณาระบุจังหวัด");
      return errors;
    },
    districtErrors() {
      const errors = [];
      if (!this.$v.s1_st_discovery.st_dis_district.$dirty) return errors;
      !this.$v.s1_st_discovery.st_dis_district.required &&
        errors.push("กรุณาระบุอำเภอ/เขต");
      return errors;
    },
    subDistrictErrors() {
      const errors = [];
      if (!this.$v.s1_st_discovery.st_dis_subDistrict.$dirty) return errors;
      !this.$v.s1_st_discovery.st_dis_subDistrict.required &&
        errors.push("กรุณาระบุตำบล/แขวง");
      return errors;
    }
  },
  watch: {
    compStep() {
      // console.log("watcherWorking...", this.compStep);
      if (this.compStep == 2) {
        this.updateToServ_patient();
      }
    }
  },
  methods: {
    async queryPatient() {
      if (this.caseProperties.manageMode == "edit") {
        this.loadingQuery = true;
        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/getCaseDetailed.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              userID: this.userProperties.userID,
              caseID: this.caseProperties.caseID,
              caseTypeID: "1"
            })
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log('getDetailed.Patient - DATA: ', dataRes)
            if (dataRes[0]["status"] == "success") {
              this.s1_pt_info = {
                pt_fname: dataRes[1]["PFName"],
                pt_lname: dataRes[1]["PLName"],
                pt_gender: dataRes[1]["Gender"],
                pt_age: dataRes[1]["Age"],
                pt_weight: dataRes[1]["Weight"],
                pt_pid: dataRes[1]["PersonalId"],
                pt_telno: dataRes[1]["TelNo"],
                is_checkForeigner: parseInt(dataRes[1]["IsForeigner"]),
                pt_national: dataRes[1]["Nationality"],
                pt_national_info: dataRes[1]["Nationality_info"]
              };

              this.s1_pt_address = {
                pt_ads_address: dataRes[1]["PAdr"],
                pt_ads_province: dataRes[1]["PProvince"],
                pt_ads_district: dataRes[1]["PDistrict"],
                pt_ads_subDistrict: dataRes[1]["PSubDistrict"]
              };

              if (dataRes[4].length > 0) {
                this.is_checkDrugAllegy = true;
                this.s1_his_arr_drugAllegy = dataRes[4];
              }
              if (dataRes[3].length > 0) {
                this.is_checkDisease = true;
                this.s1_his_arr_disease = dataRes[3];
              }

              this.s1_pt_history = {
                pt_his_favHospital: dataRes[1]["MedTreatmentPlc"],
                pt_his_conditionInfo: dataRes[1]["Condition"]
              };

              let temp_caseDate = "";
              if (dataRes[1]["CaseDate"]) {
                temp_caseDate = dataRes[1]["CaseDate"]["date"];
                temp_caseDate = temp_caseDate.substr(0, 10);
              }

              this.s1_st_info = {
                st_symptomInfo: dataRes[1]["SymptnDetails"],
                st_date: temp_caseDate,
                st_dangerType: dataRes[1]["GroupName"],
                st_dangerList: dataRes[1]["SeverityName"],
                st_dangerList_ID: dataRes[1]["SeverityId"],
                st_dangerSeverityDetail: dataRes[1]["comment"],
                st_treatment: dataRes[1]["Treatment_Name"],
                st_treatment_ID: dataRes[1]["TreatmentId"],
                st_hospital: dataRes[1]["TreatmentPlace"],
                st_hospital_province: dataRes[1]["TreatmentProvince"]
              };
              this.s1_st_discovery = {
                st_dis_address: dataRes[1]["FndAdr"]
                  ? dataRes[1]["FndAdr"]
                  : "",
                st_dis_province: dataRes[1]["FndProvince"]
                  ? dataRes[1]["FndProvince"]
                  : "",
                st_dis_district: dataRes[1]["FndDistrict"]
                  ? dataRes[1]["FndDistrict"]
                  : "",
                st_dis_subDistrict: dataRes[1]["FndSubDistrict"]
                  ? dataRes[1]["FndSubDistrict"]
                  : ""
              };

              // Get Symptom images
              if (dataRes[1]["Photos"]) {
                let splitImgPath = dataRes[1]["Photos"].split(",");
                if (splitImgPath.length > 1) {
                  splitImgPath.pop();
                }
                this.s1_photos_show = splitImgPath;
              } else {
                this.s1_photos_show = [];
              }

              // Get ProductList was related to unsymptom
              if (dataRes[2].length > 0) {
                this.list_product = dataRes[2];
              }
            }
          })
          .catch(err => {
            console.log("ERR.request.getCase.Patient: ", err);
          });
        this.loadingQuery = false;
      }
    },
    async updateToServ_patient() {
      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/postUpdate_caseUnSymptom.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            userID: this.userProperties.userID,
            caseID: this.caseProperties.caseID,
            editMODE: this.editMODE,
            s1_pt_info: this.s1_pt_info,
            s1_pt_address: this.s1_pt_address,
            s1_pt_history: this.s1_pt_history,
            s1_st_info: this.s1_st_info,
            s1_st_discovery: this.s1_st_discovery,
            s1_his_arr_drugAllegy: this.s1_his_arr_drugAllegy,
            s1_his_arr_disease: this.s1_his_arr_disease
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log('Up2Serv - DATA: ', dataRes);
          if (dataRes[0]["status"] == "failed") {
            this.snb_updateStatus = true;
            this.message_UpdateStatus =
              "เกิดข้อผิดพลาดในการอัปเดตข้อมูล กรุณาลองใหม่อีกครั้ง";
            this.snb_color = "pink";
          } else {
            if (this.loadingCaseSending && !this.editMODE) {
              this.loadingCaseSending = false;
              this.caseSendingModal = false;
              this.caseResultModal = true;
            } else {
              this.snb_updateStatus = true;
              this.message_UpdateStatus = "อัปเดตข้อมูลสำเร็จ";
              this.snb_color = "green";
            }
          }
        })
        .catch(err => {
          console.log("ERR.request.postCase.patientSymptom: ", err);
        });
    },
    async updateToServ_product() {
      // console.log("CHECK ARRAY.TESTKIT ", this.s2_testkit);
      // console.log("CHECK ARRAY.DRUGSET ", this.s2_drugSet);
      // console.log("Updated to PROD.ID ", this.temp_productID_onEdit);
      if (this.temp_productID_onEdit) {
        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/postUpdate_productAndSource.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              userID: this.userProperties.userID,
              caseID: this.caseProperties.caseID,
              productID: this.temp_productID_onEdit,
              s2_pd_detail: this.s2_pd_detail,
              s2_pd_label: this.s2_pd_label,
              s2_pd_licenseInfo: this.s2_pd_licenseInfo,
              s2_pd_manufacturer: this.s2_pd_manufacturer,
              s2_pd_usage: this.s2_pd_usage,
              s2_pd_check: this.s2_pd_check,
              s2_pd_checkInfo: this.s2_pd_checkInfo,
              s2_drugSet: this.s2_drugSet,
              s2_testkit: this.s2_testkit,
              s2_pd_duration: this.s2_pd_duration,
              s2_pd_related: this.s2_pd_related,
              s3_src_detail: this.s3_src_detail
            })
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log('Up2Serv - DATA: ', dataRes);
            if (dataRes[0]["status"] == "failed") {
              this.snb_updateStatus = true;
              this.message_UpdateStatus =
                "เกิดข้อผิดพลาดในการอัปเดตข้อมูล กรุณาลองใหม่อีกครั้ง";
              this.snb_color = "pink";
            } else {
              if (this.loadingCaseSending && !this.editMODE) {
                this.loadingCaseSending = false;
                this.caseSendingModal = false;
                this.caseResultModal = true;
              } else {
                this.snb_updateStatus = true;
                this.message_UpdateStatus = "อัปเดตข้อมูลสำเร็จ";
                this.snb_color = "green";
                this.temp_productID_onEdit = dataRes[1]["productID"];
              }
            }
          })
          .catch(err => {
            console.log("ERR.request.postCase.product: ", err);
          });
      }
    },
    async checkUserAuth_local() {
      let checkAuth = localStorage.getItem("userToken");
      if (checkAuth) {
        let userID = localStorage.getItem("userID");
        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/recheckSession.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              userID: userID,
              userToken: checkAuth
            })
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes)
            if (dataRes[0]["status"] == "failed") {
              this.$router.push("login");
              localStorage.removeItem("userToken");
              localStorage.removeItem("userID");
              localStorage.removeItem("userRole");
            } else {
              //wait for edit function and loadData from data to variable
              this.fetchGender();
              this.fetchNation();
              this.fetchProvince();
              this.fetchDangerType();
            }
          })
          .catch(err => {
            console.log(
              "ERR.requestAccess.Session_createReport.product: ",
              err
            );
          });
      } else {
        this.$router.push("/");
      }
    },
    async uploadIMG() {
      if (this.s1_st_photos.length > 0) {
        this.imgNotfound = false;
        var formData = new FormData();
        // console.log('prepare to caseProperties ', this.caseProperties);
        formData.append("reqType", "unsymptom");
        formData.append("caseID", this.caseProperties.caseID);
        formData.append("userID", this.userProperties.userID);
        // formData.append('image', this.s1_st_photos[0])
        for (let index = 0; index < this.s1_st_photos.length; index++) {
          formData.append("image[]", this.s1_st_photos[index]);
        }

        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/uploadImages.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json"
            },
            body: formData
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes, ' TTTT: ', dataRes[dataRes.length-1])
            let stillAlive = false;
            for (let index = 0; index < dataRes.length - 1; index++) {
              if (dataRes[index]["status"] != "success") {
                stillAlive = false;
                break;
              } else {
                stillAlive = true;
              }
            }
            if (stillAlive) {
              let splitImgPath = dataRes[dataRes.length - 1].split(",");
              splitImgPath.pop();
              this.s1_photos_show = splitImgPath;
              this.imgStatus = true;
            } else {
              this.imgStatus = false;
            }
          })
          .catch(err => {
            console.log("ERR.upload.img.unsymptom: ", err);
          });
      } else {
        this.imgNotfound = true;
      }
    },
    async queryProductAndChannel() {
      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/getproductDetailed_onsymptom.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
