    async getComp_s2() {
      await EventBus.$on("s2_pd_detail_case1", dataBUS => {
        // console.log("GET DATABUS.S2_PD_DETAIL CASE 1: ", dataBUS);
        this.s2_pd_detail = dataBUS.s2_pd_detail;
        this.s2_pd_label = dataBUS.s2_pd_label;
        this.s2_pd_licenseInfo = dataBUS.s2_pd_licenseInfo;
        this.s2_pd_manufacturer = dataBUS.s2_pd_manufacturer;
        this.s2_pd_usage = dataBUS.s2_pd_usage;
        this.s2_pd_check = dataBUS.s2_pd_check;
        this.s2_pd_checkInfo = dataBUS.s2_pd_checkInfo;
        this.s2_drugSet = dataBUS.s2_drugSet;
        this.s2_testkit = dataBUS.s2_testkit;
        this.s2_pd_duration = dataBUS.s2_pd_duration;
        this.s2_pd_related = dataBUS.s2_pd_related;
      });
      await EventBus.$on("s2_photos_case1", dataBUS => {
        this.s2_photos_show = dataBUS;
      });
      await EventBus.$on("backFrom_productInfo_case1", dataBUS => {
        if (dataBUS == 1) {
          this.addProduct = false;
          this.compStep = 2;
        } else if (dataBUS == 3) {
          // Call Method Update product detail to server based on productID (TawaiId)
          // console.log("SYMPTOM_REPORT.SENDING UPLOAD_IMGS AND UPD2SERV.", dataBUS);
          this.updateToServ_product();
          if (this.s2_drugSet.length > 0) {
            this.uploadIMG_Type(this.s2_drugSet, "drugSet", "drugSet");
          }
          if (this.s2_testkit.length > 0) {
            // console.log("Update IMGS To TK...");
            this.uploadIMG_Type(this.s2_testkit, "testkit", "testkit");
          }
          // console.log("GO TO DATABUS ");
          this.compStep = dataBUS;
        }
      });
    },
    async getComp_s3() {
      await EventBus.$on("s3_src_detail", dataBUS => {
        // console.log("GET DATABUS.S3_PD_SOURCE: ", dataBUS);
        this.s3_src_detail = dataBUS;
      });
      await EventBus.$on("s3_photos", dataBUS => {
        this.s3_photos_show = dataBUS;
      });
      await EventBus.$on("case_fakeAds_comp", dataBUS => {
        // console.log("GET COMP FROM S3: ", dataBUS);
        if (dataBUS == 4) {
          // Call method update source of product to server based on ProductID (CaseRelated)
          this.updateToServ_product();
          this.compStep = 2;
          this.addProduct = false;
          // Fetch - productOnUnsymptom - API
          this.queryPatient();
        } else {
          this.compStep = dataBUS;
        }
      });
    },
    nextStep(n) {
      if (n == this.steps) {
        this.caseSendingModal = true;
      } else if (n == 1) {
        if (
          this.valid_symptomAddress &&
          this.s1_st_discovery.st_dis_address.length > 0 &&
          this.s1_st_discovery.st_dis_province.length > 0 &&
          this.s1_st_discovery.st_dis_district.length > 0 &&
          this.s1_st_discovery.st_dis_subDistrict.length > 0
        ) {
          this.compStep = n + 1;
        }
      } else if (n == 2) {
        this.compStep = this.steps;
        // Call API Query all related product
        if (this.list_product.length > 0) {
          this.queryProductAndChannel();
        }
      } else {
        this.compStep = n + 1;
      }
    },
    backStep(n) {
      if (n === 1) {
        this.compStep = 1;
      } else if (n == 4) {
        this.compStep = 2;
      } else {
        this.compStep = n - 1;
      }
    },
    nextSubStep(n) {
      if (n == this.subSteps) {
        // Lock on location & address input
      } else if (n == 1) {
        this.$v.$touch();
        if (
          this.valid_userInfo &&
          this.s1_pt_info.pt_gender.length > 0 &&
          this.s1_pt_info.pt_age.length > 0 &&
          this.s1_pt_info.pt_weight.length > 0
        ) {
          this.subCompStep += 1;
        }
      } else {
        this.subCompStep += 1;
      }
    },
    backSubStep(n) {
      if (n === 1) {
        this.subCompStep = 1;
      } else {
        this.subCompStep = n - 1;
      }
    },
    exitReport() {
      this.dataMissingModal = false;
      this.$router.push("/mainuser");
    },
    async uploadIMG_Type(imageStorage, requestType, outputType) {
      if (imageStorage.length > 0) {
        // this.imgNotfound = false;
        var formData = new FormData();
        formData.append("reqType", requestType);
        formData.append("caseID", this.temp_productID_onEdit);
        formData.append("userID", this.userProperties.userID);
        formData.append("productID", this.temp_productID_onEdit);
        // formData.append('image', imageStorage[0])
        for (let index = 0; index < imageStorage.length; index++) {
          formData.append("image[]", imageStorage[index].url);
        }

        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/uploadImages.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json"
            },
            body: formData
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(requestType, 'IMG SET - DATA: ', dataRes);
            let stillAlive = false;
            for (let index = 0; index < dataRes.length - 1; index++) {
              if (dataRes[index]["status"] != "success") {
                stillAlive = false;
                break;
              } else {
                stillAlive = true;
              }
            }
            if (stillAlive) {
              let imageSet = dataRes[dataRes.length - 1];
              // console.log('IMG(TK or DS).createReport_product: ', imageSet);
              if (outputType == "drugSet") {
                for (let index = 0; index < imageSet.length; index++) {
                  this.s2_drugSet[index].url = imageSet[index];
                }
              } else if (outputType == "testkit") {
                for (let index = 0; index < imageSet.length; index++) {
                  this.s2_testkit[index].url = imageSet[index];
                }
              }
            }
          })
          .catch(err => {
            console.log("ERR.upload.img.basedOnType: ", err);
          });
      }
    },
    selectTreatment() {
      let treatINDEX = this.list_treatment.findIndex(
        treatINDEX => treatINDEX === this.s1_st_info.st_treatment
      );
      this.s1_st_info.st_treatment_ID = treatINDEX + 1;
    },
