    async editProduct_detail(index) {
      // Fetch information of product by productID
      // console.log("ProductID: ", this.list_product[index].DrugRelated);
      this.temp_productID_onEdit = this.list_product[index].DrugRelated;

      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/getProductDetailed.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            userID: this.userProperties.userID,
            caseID: this.caseProperties.caseID,
            productID: this.list_product[index].DrugRelated
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log('getDetailed.Product - DATA: ', dataRes)
          if (dataRes[0]["status"] == "success") {
            // Handler MFD
            let temp_MFD = "";
            if (dataRes[1]["ProduceDate"]) {
              temp_MFD = dataRes[1]["ProduceDate"]["date"];
              temp_MFD = temp_MFD.substr(0, 10);
            }
            // Handler EXD
            let temp_EXD = "";
            if (dataRes[1]["ExpireDate"]) {
              temp_EXD = dataRes[1]["ExpireDate"]["date"];
              temp_EXD = temp_EXD.substr(0, 10);
            }

            // Handler Date of Start Using
            let temp_DoSTART = "";
            if (dataRes[1]["StartDate"]) {
              temp_DoSTART = dataRes[1]["StartDate"]["date"];
              temp_DoSTART = temp_DoSTART.substr(0, 10);
            }
            // Handler Date of Stop Using if ContinueUsing is false
            let temp_DoSTOP = "";
            if (!dataRes[1]["IsContinueUsing"]) {
              if (dataRes[1]["StopDate"]) {
                temp_DoSTOP = dataRes[1]["StopDate"]["date"];
                temp_DoSTOP = temp_DoSTOP.substr(0, 10);
              }
            }

            this.s2_pd_detail = {
              pd_pdTypeName: dataRes[1]["ProductTypeName"],
              pd_pdTypeIndex: parseInt(dataRes[1]["ProductTypeId"]),
              pd_nameTH: dataRes[1]["ProductName"],
              pd_cNameTH: dataRes[1]["CommonNameTh"],
              pd_cNameEN: dataRes[1]["CommonNameEn"],
              pd_fNameTH: dataRes[1]["FormalNameTh"],
              pd_fNameEN: dataRes[1]["FormalNameEn"],
              pd_hpTypeName: dataRes[6]["PartName"],
              pd_hpTypeIndex: parseInt(dataRes[6]["PartId"]),
              pd_hpInfo: dataRes[6]["PartInfo"]
            };
            this.s2_pd_label = {
              pd_hasLabel: parseInt(dataRes[1]["IsHasThLabel"]),
              pd_hasLicense: parseInt(dataRes[1]["IsHasFDALicense"])
            };
            this.s2_pd_licenseInfo = {
              pdlc_number: dataRes[1]["FDACode"],
              pdlc_status: dataRes[1]["FDAStatus"],
              pdlc_isMatch: dataRes[1]["IsWrongLicense"].toString()
            };
            this.s2_pd_manufacturer = {
              pd_mf_owner: dataRes[1]["Producer"],
              pd_mf_name: dataRes[1]["ProducePlace"],
              pd_mf_address: dataRes[1]["Address"],
              pd_mf_lot: dataRes[1]["LotNo"],
              pd_mf_mfd: temp_MFD,
              pd_mf_exd: temp_EXD,
              pd_mf_newcode: dataRes[1]["NewCode"]
            };
            this.s2_pd_usage = {
              pd_us_KindName: dataRes[3]["ProductKindName"],
              pd_us_KindIndex: parseInt(dataRes[3]["ProductKindId"]),
              pd_us_KindInfo: dataRes[1]["ProductDetail"],
              pd_us_InstName: dataRes[3]["InstructionName"],
              pd_us_InstIndex: parseInt(dataRes[3]["InstructionId"]),
              pd_us_InstInfo: dataRes[1]["ProductKindIdDetail"],
              pd_us_volume: dataRes[1]["Dosage"],
              pd_us_price: parseInt(dataRes[1]["Price"]),
              pd_us_expected: dataRes[1]["Wishness"],
              pd_us_labelInfo: dataRes[1]["Properties"]
            };
            this.s2_pd_check = {
              isCheckDMSC: dataRes[1]["isCheckSciOrg"],
              isCheckQuality: dataRes[1]["isCheckLostQly"],
              isCheckAds: dataRes[1]["isCheckFakeAds"],
              isCheckTestkit: dataRes[1]["isCheckTestKit"]
            };
            this.s2_pd_checkInfo = {
              check_DMSC_status: dataRes[1]["IsInSciOrg"].toString(),
              check_DMSC_info: dataRes[1]["InSciOrgDetail"],
              check_quality_status: dataRes[1]["IsLostQly"].toString(),
              check_quality_info: dataRes[1]["LostQlyDetail"],
              check_ads_status: dataRes[1]["IsFakeAds"].toString(),
              check_ads_info: dataRes[1]["FakeAdsDetail"]
            };
            this.s2_drugSet = dataRes[4];
            this.s2_testkit = dataRes[5];

            // Using and extraCheck
            this.s2_pd_duration = {
              pd_dur_dateOfUse: temp_DoSTART,
              pd_dur_dateOfStop: temp_DoSTOP,
              pd_dur_contUse: dataRes[1]["IsContinueUsing"]
            };
            this.s2_pd_related = {
              isCheck_related: true,
              pd_check_relatedID: dataRes[1]["RelatedId"],
              pd_check_relatedName: dataRes[1]["RelatedName"]
            };

            // handler onAir Date
            let temp_extractDate = "";
            if (dataRes[2]["date"]) {
              temp_extractDate = dataRes[2]["date"]["date"];
              temp_extractDate = temp_extractDate.substr(0, 10);
            }

            var subSrcName = "";
            if (dataRes[2]["SubChannelName"]) {
              subSrcName = dataRes[2]["SubChannelName"];
            } else {
              subSrcName = dataRes[2]["subName"];
            }

            this.s3_src_detail = {
              src_srcTypeName: dataRes[2]["ChannelName"],
              src_srcTypeIndex: dataRes[2]["ChannelId"],
              src_subSrcTypeName: subSrcName,
              src_subSrcTypeIndex: dataRes[2]["ChannelSubId"],
              src_subInfoName: dataRes[2]["Name"],
              src_dateOnAir: temp_extractDate,
              src_timeOnAir: dataRes[2]["Period"],
              src_adrs: dataRes[2]["Adr"],
              src_prov: dataRes[2]["Province"],
              src_dist: dataRes[2]["District"],
              src_subDist: dataRes[2]["SubDistrict"],
              src_pName: dataRes[2]["details"],
              src_pTel: dataRes[2]["TelNo"],
              src_pContact: dataRes[2]["ContactMethod"],
              src_pLicense: dataRes[2]["LicenseNo"],
              src_pCar: dataRes[2]["CarBrand"],
              src_pCarColor: dataRes[2]["CarColor"]
            };

            if (dataRes[1]["photos"]) {
              let splitImgPath = dataRes[1]["photos"].split(",");
              if (splitImgPath.length > 1) {
                splitImgPath.pop();
              }
              this.s2_photos_show = splitImgPath;
            } else {
              this.s2_photos_show = [];
            }
            if (dataRes[2]["srcPhotos"]) {
              let splitImgPath = dataRes[2]["srcPhotos"].split(",");
              if (splitImgPath.length > 1) {
                splitImgPath.pop();
              }
              this.s3_photos_show = splitImgPath;
            } else {
              this.s3_photos_show = [];
            }

            this.addProduct = true;
          } // END IF: Success
        })
        .catch(err => {
          console.log("ERR.request.getCase.Product: ", err);
        });
    },
    delProduct_confirm(index) {
      // Show modal box to confirm before delete
      this.productDelModal = true;
      this.temp_productID_onDel = this.list_product[index]["DrugRelated"];
    },
    async delProduct_detail() {
      // Call API delete product
      this.loadingCaseSending = true;
      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/postDelete_productAndSource.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            userID: this.userProperties.userID,
            caseID: this.caseProperties.caseID,
            productID: this.temp_productID_onDel
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log('request.delete - DATA: ', dataRes);
          if (dataRes[0]["status"] == "failed") {
            this.snb_updateStatus = true;
            this.message_UpdateStatus =
              "เกิดข้อผิดพลาดในการลบผลิตภัณฑ์ กรุณาลองใหม่อีกครั้ง";
            this.snb_color = "pink";
          } else {
            this.snb_updateStatus = true;
            this.message_UpdateStatus = "ลบผลิตภัณฑ์สำเร็จ";
            this.snb_color = "green";
            this.queryPatient();
          }
        })
        .catch(err => {
          console.log("ERR.request.delete.ProductAndChannel: ", err);
        });
      this.loadingCaseSending = false;
      this.productDelModal = false;
    },
    sendingCase() {
      this.editMODE = false;
      this.loadingCaseSending = true;
      this.updateToServ_patient();
    },
    confirmResult() {
      this.caseResultModal = false;
      this.$router.push("/mainuser");
      this.$destroy();
    }
  },
  created() {
    this.userProperties = this.$store.getters.getUserProperties;
    this.caseProperties = this.$store.getters.getCaseProperties;
    if (this.userProperties.userID.length < 1) {
      this.dataMissingModal = true;
    }
  },
  mounted() {
    this.checkUserAuth_local();
    this.queryPatient();
    this.getComp_s2();
    this.getComp_s3();
  }
};
</script>

