<template>
  <div class="bodyContent">
    <!-- S1: Patient and Unsymptom information -->
    <v-row>
      <v-col cols="12" align="start">
        <p class="header_form">รายละเอียดผู้ป่วยและอาการไม่พึงประสงค์</p>
        <!-- S1_1: Patient Info. -->
        <v-row align="center" class="px-4">
          <p class="sub-header_form">ข้อมูลผู้ป่วย: ประวัติส่วนบุคคล</p>
          <v-divider class="ml-4 pb-3"></v-divider>
        </v-row>
        <v-row justify="start" class="px-4 mt-4">
          <v-col cols="6" md="6" align="left" class="py-0">
            <v-text-field
              label="ชื่อ"
              outlined
              rounded
              v-model="s1_pt_info.pt_fname"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" align="left" class="py-0">
            <v-text-field
              label="นามสกุล"
              outlined
              rounded
              v-model="s1_pt_info.pt_lname"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" align="left" class="py-0">
            <v-text-field
              label="เพศ *"
              outlined
              rounded
              v-model="s1_pt_info.pt_gender"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" align="left" class="py-0">
            <v-text-field
              label="อายุ (ปี) *"
              outlined
              rounded
              v-model="s1_pt_info.pt_age"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" align="left" class="py-0">
            <v-text-field
              label="น้ำหนัก (กิโลกรัม) *"
              outlined
              rounded
              v-model="s1_pt_info.pt_weight"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" align="left" class="py-0">
            <v-text-field
              label="เบอร์โทรศัพท์ติดต่อ"
              outlined
              rounded
              v-mask="mask_tel"
              v-model="s1_pt_info.pt_telno"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="6" align="left" class="py-0">
            <v-text-field
              label="หมายเลขประจำตัวประชาชนหรือหนังสือเดินทาง"
              outlined
              rounded
              v-model="s1_pt_info.pt_pid"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="6" align="left" class="py-0 mb-4">
            <v-checkbox
              class="py-0"
              v-model="s1_pt_info.is_checkForeigner"
              label="ผู้ป่วยเป็นชาวต่างชาติหรือไม่"
              readonly
            >
            </v-checkbox>
          </v-col>
          <v-col
            v-if="s1_pt_info.is_checkForeigner"
            cols="12"
            md="6"
            align="left"
            class="py-0"
          >
            <v-text-field
              label="เชื้อชาติ"
              outlined
              rounded
              v-model="s1_pt_info.pt_national"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col
            v-if="s1_pt_info.pt_national == 'อื่น ๆ'"
            cols="12"
            md="6"
            align="left"
            class="py-0"
          >
            <v-text-field
              label="ระบุเชื้อชาติ"
              outlined
              rounded
              v-model="s1_pt_info.pt_national_info"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row justify="start" class="px-4">
          <v-col cols="12" align="left" class="py-0">
            <v-text-field
              label="ที่อยู่ผู้ป่วย"
              outlined
              rounded
              v-model="s1_pt_address.pt_ads_address"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label=" จังหวัด"
              outlined
              rounded
              v-model="s1_pt_address.pt_ads_province"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>

          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label="อำเภอ/เขต"
              outlined
              rounded
              v-model="s1_pt_address.pt_ads_district"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>

          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label="ตำบล/แขวง"
              outlined
              rounded
              v-model="s1_pt_address.pt_ads_subDistrict"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
        </v-row>

        <!-- S1_2: Patient History -->
        <v-row align="center" class="px-4">
          <p class="sub-header_form">
            ข้อมูลผู้ป่วย: ประวัติแพ้ยาและโรคประจำตัว
          </p>
          <v-divider class="ml-4 pb-3"></v-divider>
        </v-row>
        <v-row justify="start" class="px-4">
          <v-col cols="12" md="6" class="py-0">
            <v-checkbox
              class="py-0"
              input-value="s1_his_arr_drugAllegy.length > 0 ? true : false"
              label="ผู้ป่วยมีประวัติแพ้ยาหรือไม่"
              readonly
            >
            </v-checkbox>
          </v-col>
        </v-row>
        <div
          v-if="s1_his_arr_drugAllegy.length > 0 ? true : false"
          class="px-4 mt-4"
        >
          <!-- allegy detail -->
          <v-row v-for="(content, index) in s1_his_arr_drugAllegy" :key="index">
            <v-col cols="12" md="1" class="pt-4">
              <span>ลำดับที่ {{ index + 1 }}</span>
              <v-divider class="mb-4"> </v-divider>
            </v-col>
            <v-col cols="12" md="5" class="py-0">
              <v-text-field
                class="my-0"
                label="ชื่อยา"
                outlined
                rounded
                v-model="content.drug"
                background-color="grey lighten-4"
                readonly
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="5" class="py-0">
              <v-text-field
                class="my-0"
                label="ชื่ออาการแพ้ยา"
                outlined
                rounded
                v-model="content.symptom"
                background-color="grey lighten-4"
                readonly
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="12" class="py-0">
              <v-checkbox
                class="mt-3"
                label="มีบัตรแพ้ยา"
                v-model="content.allegyCard"
                readonly
              ></v-checkbox>
              <v-divider class="mb-4"> </v-divider>
            </v-col>
          </v-row>
        </div>

        <v-row justify="start" class="px-4">
          <v-col cols="12" md="6" class="py-0">
            <v-checkbox
              class="py-0"
              input-value="s1_his_arr_disease.length > 0 ? true : false"
              label="ผู้ป่วยมีโรคประจำตัวหรือไม่"
              readonly
            >
            </v-checkbox>
          </v-col>
        </v-row>
        <div
          v-if="s1_his_arr_disease.length > 0 ? true : false"
          class="px-4 mt-4"
        >
          <!-- disease detail -->
          <v-row v-for="(content, index) in s1_his_arr_disease" :key="index">
            <v-col cols="12" md="1" class="pt-4">
              <span>ลำดับที่ {{ index + 1 }}</span>
              <v-divider class="mb-4"> </v-divider>
            </v-col>
            <v-col cols="12" md="10" class="py-0">
              <v-text-field
                class="my-0"
                label="ชื่อโรคประจำตัว"
                outlined
                rounded
                v-model="content.symptom"
                background-color="grey lighten-4"
                readonly
              ></v-text-field>
            </v-col>
          </v-row>
        </div>

        <v-row class="px-4 mt-4">
          <v-col cols="12" md="6" class="py-0">
            <v-textarea
              label="สถานพยาบาลที่ทำการรักษาประจำ"
              auto-grow
              outlined
              rounded
              v-model="s1_pt_history.pt_his_favHospital"
              background-color="grey lighten-4"
              readonly
            ></v-textarea>
          </v-col>
          <v-col cols="12" md="6" class="py-0">
            <v-textarea
              label="ระบุภาวะอื่นที่เกี่ยวข้อง เช่น การตั้งครรถ์ ให้นมบุตร G6PD หรืออื่น ๆ ให้ระบุ"
              auto-grow
              outlined
              rounded
              v-model="s1_pt_history.pt_his_conditionInfo"
              background-color="grey lighten-4"
              readonly
            ></v-textarea>
          </v-col>
        </v-row>

        <!-- S1_3: Unsymptom Info. -->
        <v-row align="center" class="px-4">
          <p class="sub-header_form">ข้อมูลอาการไม่พึงประสงค์</p>
          <v-divider class="ml-4 pb-3"></v-divider>
        </v-row>
        <v-row justify="start" class="px-4 mt-4">
          <DisplayImages :imagesArray="s1_photos_show" />
        </v-row>
        <v-row justify="start" class="px-4 mt-4">
          <v-col cols="12" md="9" class="py-0">
            <v-text-field
              label="รายละเอียดอาการ"
              outlined
              rounded
              v-model="s1_st_info.st_symptomInfo"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="6" class="py-0">
            <v-text-field
              :value="DateFormatted"
              outlined
              rounded
              label="วันที่พบอาการ"
              readonly
              background-color="grey lighten-4"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row justify="start" class="px-4">
          <v-col cols="6" align="left" class="py-0">
            <v-text-field
              label="เลือกระดับความรุนแรง"
              outlined
              rounded
              v-model="s1_st_info.st_dangerType"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col
            v-if="
              s1_st_info.st_dangerType == 'ร้ายแรง' ||
                s1_st_info.st_dangerType == 'ไม่ร้ายแรง'
            "
            cols="6"
            align="left"
            class="py-0"
          >
            <v-text-field
              label="เลือกประเภทความร้ายแรง"
              outlined
              rounded
              v-model="s1_st_info.st_dangerList"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col
            v-if="
              s1_st_info.st_dangerList == 'อื่น ๆ ที่มีความสำคัญทางการแพทย์'
            "
            cols="12"
            align="left"
            class="py-0"
          >
            <v-text-field
              label="กรอกรายละเอียดความสำคัญทางการแพทย์"
              outlined
              rounded
              v-model="s1_pt_info.st_dangerSeverityDetail"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
        </v-row>
        <!-- Treatment Detail -->
        <div v-if="s1_st_info.st_dangerType == 'ไม่ร้ายแรง'">
          <v-row class="px-4">
            <v-col cols="12" md="6" class="py-0">
              <v-text-field
                label="สถานะการรักษาพยาบาล"
                outlined
                rounded
                v-model="s1_st_info.st_treatment"
                background-color="grey lighten-4"
                readonly
              ></v-text-field>
            </v-col>
            <v-col
              v-if="
                s1_st_info.st_treatment_ID == 1 ||
                  s1_st_info.st_treatment_ID == 2
              "
              cols="12" md="6"
              class="py-0"
            >
              <v-text-field
                label="ระบุโรงพยาบาล"
                outlined
                rounded
                v-model="s1_st_info.st_hospital"
                background-color="grey lighten-4"
                readonly
              ></v-text-field>
            </v-col>
            <v-col
              v-if="
                s1_st_info.st_treatment_ID == 1 ||
                  s1_st_info.st_treatment_ID == 2
              "
              cols="9" md="6"
              class="py-0"
            >
              <v-text-field
                label=" เลือกจังหวัด"
                outlined
                rounded
                v-model="s1_st_info.st_hospital_province"
                background-color="grey lighten-4"
                readonly
              ></v-text-field>
            </v-col>
          </v-row>
        </div>
        <!-- Location -->
        <v-row class="px-4">
          <v-col cols="12" class="py-0">
            <v-text-field
              label="สถานที่พบอาการ/เกิดเหตุ"
              outlined
              rounded
              v-model="s1_st_discovery.st_dis_address"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label=" จังหวัด"
              outlined
              rounded
              v-model="s1_st_discovery.st_dis_province"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>

          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label="อำเภอ/เขต"
              outlined
              rounded
              v-model="s1_st_discovery.st_dis_district"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>

          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label="ตำบล/แขวง"
              outlined
              rounded
              v-model="s1_st_discovery.st_dis_subDistrict"
              background-color="grey lighten-4"
              readonly
            ></v-text-field>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <v-divider></v-divider>

    <!-- S2 and S3: Product and Channel information -->
    <v-row>
      <v-col cols="12" align="start">
        <p class="header_form">ผลิตภัณฑ์ที่เกี่ยวข้อง</p>

        <div
          v-if="
            typeof this.s2_3_information[0] !== 'undefined' &&
              s2_3_information[1].length > 0
          "
        >
          <v-expansion-panels>
            <v-expansion-panel
              v-for="(item, i) in s2_3_information[1]"
              :key="i"
            >
              <v-expansion-panel-header>
                <span class="sub-header_form"
                  >ผลิตภัณฑ์รายการที่ {{ i + 1 }} :
                  {{ item.ProductTypeName }}</span
                >
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <div>
                  <!-- Product's Photos -->
                  <v-row class="ma-4">
                    <v-col cols="12" class="py-0" align="center">
                      <div v-if="item.photos">
                        <DisplayImages :imagesArray="item.photos" />
                      </div>
                      <div v-else>
                        <span>ไม่พบรูปภาพผลิตภัณฑ์</span>
                      </div>
                    </v-col>
                  </v-row>
                  <!-- Product's Detailed -->
                  <v-row align="center" class="px-4">
                    <p class="sub-header_form">ประเภทผลิตภัณฑ์</p>
                    <v-divider class="ml-4 pb-3"></v-divider>
                  </v-row>
                  <v-row justify="start">
                    <v-col cols="6" align="left" class="px-6">
                      <v-text-field
                        label="ประเภทผลิตภัณฑ์"
                        outlined
                        rounded
                        v-model="item.ProductTypeName"
                        background-color="grey lighten-4"
                        readonly
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row align="center" class="px-4">
                    <p class="sub-header_form">การขึ้นทะเบียน</p>
                    <v-divider class="ml-4 pb-3"></v-divider>
                  </v-row>
                  <v-row justify="start">
                    <v-col cols="12" md="12" class="py-0 px-6" align="left">
                      <!-- CheckBox License -->
                      <v-row>
                        <v-col cols="12" md="6" class="py-0">
                          <v-checkbox
                            class="py-0"
                            v-model="item.IsHasThLabel"
                            label="ท่านพบฉลากภาษาไทยหรือไม่"
                            readonly
                          >
                          </v-checkbox>
                        </v-col>
                        <v-col cols="12" md="6" class="py-0">
                          <v-checkbox
                            class="py-0"
                            v-model="item.IsHasFDALicense"
                            label="มีการขึ้นทะเบียนผลิตภัณฑ์ อย. หรือไม่"
                            readonly
                          >
                          </v-checkbox>
                        </v-col>
                      </v-row>
                      <!-- License Detail -->
                      <v-row align="start">
                        <v-col cols="6" md="6" class="py-0 mt-3">
                          <v-text-field
                            label="เลขทะเบียน อย."
                            outlined
                            rounded
                            v-model="item.FDACode"
                            append-icon="fa-search"
                            background-color="grey lighten-4"
                            readonly
                          ></v-text-field>
                        </v-col>
                        <v-col cols="6" md="6" class="py-0 mt-3">
                          <v-text-field
                            label="สถานะการขึ้นทะเบียน"
                            outlined
                            rounded
                            v-model="item.FDAStatus"
                            background-color="grey lighten-4"
                            readonly
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6" class="py-0">
                          <span
                            >ข้อมูลผลิตภัณฑ์ที่พบตรงกับฐานข้อมูล อย.
                            หรือไม่</span
                          >
                          <v-radio-group
                            :value="item.IsWrongLicense.toString()"
                            row
                            :readonly="!item.IsHasFDALicense"
                          >
                            <v-radio
                              label="ไม่ตรง (ปลอม/สวมทะเบียน)"
                              value="0"
                            ></v-radio>
                            <v-radio label="ตรง" value="1"></v-radio>
                          </v-radio-group>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>

                  <v-row align="center" class="px-4">
                    <p class="sub-header_form">ชื่อผลิตภัณฑ์</p>
                    <v-divider class="ml-4 pb-3"></v-divider>
                  </v-row>
                  <!-- Name, First Row -->
                  <v-row justify="start">
                    <v-col cols="12" md="12" class="py-0 px-6" align="left">
                      <v-row class="pt-4">
                        <v-col
                          v-if="item.ProductTypeId == 3"
                          cols="12"
                          md="6"
                          class="py-0"
                        >
                          <v-text-field
                            label="ชื่อผลิตภัณฑ์ (ทั่วไป)"
                            v-model="item.ProductName"
                            background-color="grey lighten-4"
                            readonly
                            outlined
                            rounded
                          ></v-text-field>
                        </v-col>
                        <v-col
                          v-if="item.ProductTypeId != 3"
                          cols="12"
                          md="6"
                          class="py-0"
                        >
                          <v-text-field
                            label="ชื่อสามัญ (ภาษาไทย)"
                            v-model="item.CommonNameTh"
                            background-color="grey lighten-4"
                            readonly
                            outlined
                            rounded
                          ></v-text-field>
                        </v-col>
                        <v-col
                          v-if="item.ProductTypeId != 3"
                          cols="12"
                          md="6"
                          class="py-0"
                        >
                          <v-text-field
                            label="ชื่อสามัญ (ภาษาอังกฤษ)"
                            v-model="item.CommonNameEn"
                            background-color="grey lighten-4"
                            readonly
                            outlined
                            rounded
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-col>
                  </v-row>
                  <!-- Name, Seconde Row -->
                  <v-row justify="start">
                    <v-col cols="12" md="12" class="py-0 px-6" align="left">
                      <v-row>
                        <v-col
                          v-if="item.ProductTypeId != 3"
                          cols="12"
                          md="6"
                          class="py-0"
                        >
                          <v-text-field
                            label="ชื่อทางการค้า (ภาษาไทย)"
                            v-model="item.FormalNameTh"
                            background-color="grey lighten-4"
                            readonly
                            outlined
                            rounded
                          ></v-text-field>
                        </v-col>
                        <v-col
                          v-if="item.ProductTypeId != 3"
                          cols="12"
                          md="6"
                          class="py-0"
                        >
                          <v-text-field
