                  <v-row class="ma-4">
                    <v-col cols="12" class="py-0" align="center">
                      <div v-if="s2_3_information[2][i].srcPhotos">
                        <DisplayImages
                          :imagesArray="s2_3_information[2][i].srcPhotos"
                        />
                      </div>
                      <div v-else>
                        <span>ไม่พบรูปภาพผลิตภัณฑ์</span>
                      </div>
                    </v-col>
                  </v-row>
                  <p class="sub-header_form">
                    ประเภทแหล่งที่มา/จุดเผยแพร่โฆษณา
                  </p>
                  <v-row class="px-4">
                    <v-col cols="12" md="6" class="py-0">
                      <v-text-field
                        label="ประเภทของแหล่งที่มา"
                        v-model="s2_3_information[2][i].ChannelName"
                        background-color="grey lighten-4"
                        readonly
                        outlined
                        rounded
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <!-- S3.1: About Source -->
                  <p class="sub-header_form">เกี่ยวกับแหล่งที่มา/จุดเผยแพร่</p>
                  <v-textarea
                    class="mt-4 px-4"
                    label="เกี่ยวกับแหล่งที่มา/จุดที่เผยแพร่"
                    :value="
                      getAboutSource(
                        s2_3_information[2][i].SubChannelName,
                        s2_3_information[2][i].subName,
                        s2_3_information[2][i].Name,
                        s2_3_information[2][i].date.date,
                        s2_3_information[2][i].Period,
                        s2_3_information[2][i].details,
                        s2_3_information[2][i].TelNo,
                        s2_3_information[2][i].ContactMethod,
                        s2_3_information[2][i].LicenseNo,
                        s2_3_information[2][i].CarBrand,
                        s2_3_information[2][i].CarColor
                      )
                    "
                    filled
                    background-color="grey lighten-4"
                    readonly
                    rounded
                    outlined
                    no-resize
                    rows="3"
                  >
                  </v-textarea>

                  <!-- S3.2: Location Info -->
                  <p class="sub-header_form">ตำแหน่ง/สถานที่พบจุดเผยแพร่</p>
                  <v-row class="px-4">
                    <v-col cols="12" class="py-0">
                      <v-text-field
                        label="ที่อยู่"
                        v-model="s2_3_information[2][i].Adr"
                        background-color="grey lighten-4"
                        readonly
                        outlined
                        rounded
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="px-4">
                    <v-col cols="6" md="4" class="py-0">
                      <v-text-field
                        label="จังหวัด"
                        v-model="s2_3_information[2][i].Province"
                        background-color="grey lighten-4"
                        readonly
                        outlined
