                      ></v-text-field>
                    </v-col>
                    <v-col cols="6" md="4" class="py-0">
                      <v-text-field
                        label="อำเภอ/เขต"
                        v-model="s2_3_information[2][i].District"
                        background-color="grey lighten-4"
                        readonly
                        outlined
                        rounded
                      ></v-text-field>
                    </v-col>
                    <v-col cols="6" md="4" class="py-0">
                      <v-text-field
                        label="ตำบล/แขวง"
                        v-model="s2_3_information[2][i].SubDistrict"
                        background-color="grey lighten-4"
                        readonly
                        outlined
                        rounded
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </div>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </div>

        <div v-else>
          <p class="sub-header_form">ไม่พบข้อมูลผลิตภัณฑ์ที่เกี่ยวข้อง</p>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mask } from "vue-the-mask";
import moment from "moment";

import DisplayImages from "./DisplayImages";
export default {
  directives: { mask },
  components: {
    DisplayImages
  },
  props: {
    s1_photos_show: Array,
    s1_pt_info: Object,
    s1_pt_address: Object,
    s1_his_arr_disease: Array,
    s1_his_arr_drugAllegy: Array,
    s1_pt_history: Object,
    s1_st_info: Object,
    s1_st_discovery: Object,
    s2_3_information: {
      type: Array,
      default: () => [[{ status: "failed" }], []]
    }
  },
  data: () => ({
    // LIST STORAGE
    mask_tel: "###-###-####",

    // CHECK
    is_checkDrugAllegy: false,
    is_checkDisease: false,

    // VARIABLES
    userRole: 0,
    alwayCheckTrue: true
  }),
  computed: {
    DateFormatted() {
      let originYear = this.s1_st_info.st_date.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s1_st_info.st_date.replace(originYear, cvYearBE);
      return this.s1_st_info.st_date
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    }
  },
  methods: {
    DateFormatted_moment(date) {
      let originYear = date.substr(0, 4);
      if (parseInt(originYear) < 2000) {
        return "";
      }
      let cvYearInt = parseInt(originYear);
      let cvYearBE = (cvYearInt += 543);
      let displayDate = date.replace(originYear, cvYearBE);
      return date
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    },
    getAboutSource(
      SubChannelName,
      subName,
      Name,
      date,
      Period,
      details,
      TelNo,
      ContactMethod,
      LicenseNo,
      CarBrand,
      CarColor
    ) {
      return (
        SubChannelName +
        " " +
        subName +
        " " +
        Name +
        " " +
        this.DateFormatted_moment(date) +
        " " +
        Period +
        " " +
        details +
        " " +
        TelNo +
        " " +
        ContactMethod +
        " " +
        LicenseNo +
        " " +
        CarBrand +
        " " +
        CarColor
      );
    }
  },
  created() {
    //
  },
  mounted() {
    this.userRole = localStorage.getItem("userRole");
  }
};
</script>

